export interface WykresXYDane {
  label: string;
  dane0?: number;
  dane1?: number;
  dane2?: number;
  dane3?: number;
  dane4?: number;
  dane5?: number;
  dane6?: number;
  dane7?: number;
  dane8?: number;
  dane9?: number;
}

export function jednostkaAndDivider(dane_xy: WykresXYDane[], jednostka: string, pc_divider = 1) {
  let j = jednostka;
  if (jednostka && jednostka?.indexOf("auto") > -1) {
    let max = 0;
    dane_xy?.forEach(v => {
      if (v.dane0 && v.dane0 / pc_divider > max) max = v.dane0 / pc_divider;
      if (v.dane1 && v.dane1 / pc_divider > max) max = v.dane1 / pc_divider;
      if (v.dane2 && v.dane2 / pc_divider > max) max = v.dane2 / pc_divider;
      if (v.dane3 && v.dane3 / pc_divider > max) max = v.dane3 / pc_divider;
      if (v.dane4 && v.dane4 / pc_divider > max) max = v.dane4 / pc_divider;
      if (v.dane5 && v.dane5 / pc_divider > max) max = v.dane5 / pc_divider;
    });
    if (max > 1e10) {
      j = j.replace("auto", "mld");
    } else if (max > 1e7) {
      j = j.replace("auto", "mln");
    } else if (max > 1e4) {
      j = j.replace("auto", "tys.");
    } else {
      j = j.replace("auto", "");
    }
  }
  const jednostka_divider = j.indexOf('tys.') > -1?1e3:
    (j.indexOf('mln') > -1?1e6:
      (j.indexOf('mld') > -1?1e9:1));
  const numberDivider = (value: number) => {
    return Math.floor(value / (jednostka_divider * pc_divider));
  }
  return {j, numberDivider}
}